<template>
  <div class="chsi-cropper">
    <div style="margin-bottom: 20px;">
      <van-button type="primary" size="mini" @click="changeScale(1)">放大</van-button>
      <van-button type="primary" size="mini" @click="changeScale(-1)">缩小</van-button>
      <van-button type="primary" size="mini" @click="rotateLeft">左旋转</van-button>
      <van-button type="primary" size="mini" @click="rotateRight">右旋转</van-button>
      <van-button type="primary" size="mini" @click="down('blob')">下载</van-button>
    </div>
    <van-uploader :before-read="beforeRead" />
    <div class="cropper-content">
      <div class="cropper" style="text-align:center">
          <vue-cropper
            ref="cropper"
            :img="option.img"
            :outputSize="option.size"
            :outputType="option.outputType"
            :info="true"
            :full="option.full"
            :canMove="option.canMove"
            :canMoveBox="option.canMoveBox"
            :original="option.original"
            :autoCrop="option.autoCrop"
            :autoCropWidth="option.autoCropWidth"
            :autoCropHeight="option.autoCropHeight"
            :fixedBox="option.fixedBox"
            @realTime="realTime"
            @imgLoad="imgLoad"
            @touchstart.prevent="touchStart"
            @touchmove.prevent="touchMove"
          ></vue-cropper>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data() {
    return {
      option: {
        img: '', // 裁剪图片的地址
        info: true, // 裁剪框的大小信息
        outputSize:1, //剪切后的图片质量（0.1-1）
        full: false,//输出原图比例截图 props名full
        outputType: 'png',
        canMove: true, 
        original: false, 
        autoCrop: true, 
        autoCropWidth: 240, 
        autoCropHeight: 320, 
        fixedBox: true,
        fixed: true,
        fixedNumber: [4, 3],
        canMoveBox: false,
        centerBox: true,
        infoTrue: true
      } 
    }
  },
  methods: {
    getDinstance(point0, point1) {
      return Math.sqrt(Math.pow(point0.pageY - point1.pageY, 2) + Math.pow(point0.pageX - point1.pageX, 2))
    },
    getFocalPoint (point0, point1) {
      return {
        x: (point0.pageX + point1.pageX) / 2,
        y: (point0.pageY + point1.pageY) / 2
      }
    },
    touchMove(event) {
      var fingerCount = event.touches.length
      if (fingerCount) {
        // 记录触摸初始位置
        let touchEvent = event.touches[0]
        this.touchPos = {
          x: touchEvent.clientX,
          y: touchEvent.clientY
        }
      }
      if (fingerCount >= 2) {
        // 获取两点距离、中点位置；两点距离old/new=放大倍数；中点位置，缩放中心；
        let point0 = event.touches[0]
        let point1 = event.touches[1]
        this.distance = this.getDinstance(point0, point1)
        this.touchPos = this.getFocalPoint(point0, point1)
        // 设置缩放倍数，
      }
    },
    touchStart(event) {

    },
    beforeRead(file) {
      console.log(file)
      var data = window.URL.createObjectURL(file);
      console.log(data)
      this.option.img = data;
      return false;
    },
    changeScale(num) { 
      console.log('changeScale')
      num = num || 1; 
      this.$refs.cropper.changeScale(num); 
    }, 
    //坐旋转
    rotateLeft() { 
      console.log('rotateLeft')
      this.$refs.cropper.rotateLeft(); 
    }, 
    //右旋转
    rotateRight() { 
      console.log('rotateRight')
      this.$refs.cropper.rotateRight(); 
    },
    // 实时预览函数 
    realTime(data) { 
      console.log('realTime')
      this.previews = data 
    }, 
    //下载图片
    down(type) { 
      console.log('down')
      var aLink = document.createElement('a') 
      aLink.download = 'author-img' 
      if (type === 'blob') { 
        this.$refs.cropper.getCropBlob((data) => { 
          this.downImg = window.URL.createObjectURL(data)
          aLink.href = window.URL.createObjectURL(data) 
          aLink.click() 
        }) 
      } else { 
        this.$refs.cropper.getCropData((data) => { 
          this.downImg = data;
          aLink.href = data; 
          aLink.click() 
        }) 
      } 
    }, 
    imgLoad (msg) { 
      console.log('imgLoad')
      console.log(msg) 
    }
  }
}
</script>

